<template>
  <div id='app'>
    <div  id="head_part" class="container-fluid" style="padding: unset">
      <Header img="" school='Go语言实战抽奖系统'></Header>
    </div>
    <div id="video_part" class="row pt-6">
      <div class="col-xs-12 col-md-7 col-md-offset-1 p-2">
        <div v-if="showflg" class="pull-left">
          <video id="video1" controls controlsList="nodownload"
                 style="width: 800px;height:500px">
            <source :src="vUrl" type="video/mp4">
          </video>
        </div>
      </div>
      <div class="col-xs-12 col-md-3 ml-4">
        <div class="col-xs-12 font-large font-bold" style="color: wheat">播放列表&nbsp;<i class="el-icon-video-camera-solid" style="font-size: 20px;padding-top: 2px;"></i></div>
        <div class="col-xs-12 col-md-12 video-list-bg">
          <div class="row mt-3" @click="changeVideo(video.materialUrl)" v-for="(video, index) in videoList" v-bind:key="index" :class="[video.materialUrl === vUrl ?'video-selected':'video-bg']">
            <div class="col-md-4">
              <img class="video-img" src="../../../assets/img/video-material.png">
            </div>
            <div class="col-md-8 pull-left pointer" style="padding-top:10%">
              {{video.materialName}}
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-md-7 col-md-offset-1 p-2">
        <div class="pointer font-large font-bold mt-4 comment-title" >全部评论&nbsp;({{commentList.length}})</div>
        <div style="margin: 10px;padding: 10px">
          <p v-if="commentList.length==0">暂无评论，我来发表第一篇评论！</p>
          <div v-else>
            <div v-for="(item,index) in commentList" v-bind:key="index">
            <div class="col-md-1">
              <img src="../../../assets/img/user.png" style="width: 100%;border-radius: 50%">
            </div>
            <div class="comment col-md-11" >
              <b>{{item.name}}&nbsp;&nbsp;<span>{{item.time}}</span></b>
              <p @click="changeCommenter(item.name,index)">{{item.content}}</p>
              <div v-if="item.reply.length > 0">
                <div class="reply" v-for="(reply,indexq) in item.reply" v-bind:key="indexq">
                  <b>{{reply.responder}}&nbsp;&nbsp;回复&nbsp;&nbsp;{{reply.reviewers}}&nbsp;&nbsp;<span>{{reply.time}}</span></b>
                  <p @click="changeCommenter(reply.responder,index)">{{reply.content}}</p>
                </div>
              </div>
            </div>
            </div>
          </div>
          <!--回复区域 end-->
        </div>
      </div>
    </div>
    <!--<div id="videoDialog"></div>-->
  </div>
</template>
<script>
import Header from '../../../components/school-training/Header.vue'
export default {
  name: 'app',
  components: {
    Header
  },
  mounted () {
    $('#video1').bind('contextmenu', function () { return false })
    $('#video_part').height(document.documentElement.clientHeight - $('#head_part').height() - 21)
    $('#video_part').css({
      'background-color': '#292929'
    })
    this.initData()
  },
  data () {
    return {
      vdHeight: '1',
      bdHeight: '',
      marginTop: '',
      vUrl: '',
      showflg: true,
      videoList: [],
      docList: [],
      commentList: [
        {
          name: '有毒的黄同学',
          time: '2016-08-17',
          content: '好,讲得非常好，good',
          reply: [
            {
              responder: '有毒的黄同学',
              reviewers: '傲娇的',
              time: '2016-09-05',
              content: '你说得对'
            },
            {
              responder: '傲娇的',
              reviewers: '有毒的黄同学',
              time: '2016-09-05',
              content: '很强'
            }
          ]
        },
        {
          name: '有毒的黄同学',
          time: '2016-08-17',
          content: '好,讲得非常好，good',
          reply: [
            {
              responder: '有毒的黄同学',
              reviewers: '傲娇的',
              time: '2016-09-05',
              content: '你说得对'
            },
            {
              responder: '傲娇的',
              reviewers: '有毒的黄同学',
              time: '2016-09-05',
              content: '很强'
            }
          ]
        },
        {
          name: 'Freedom小黄',
          time: '2016-08-17',
          content: '好,讲得非常好，good',
          reply: []
        }
      ]
    }
  },
  methods: {
    initData: function () {
      let me = this
      // document.body.style.backgroundColor = '#27272f'
      // this.vdHeight = 300
      // this.bdHeight = window.innerHeight
      // this.marginTop = (this.bdHeight - this.vdHeight) / 2
      // this.marginTop = this.marginTop + 'px'
      this.vUrl = localStorage.getItem('materialVideo')
      this.showflg = true
      // 获取视频列表
      me.projectId = localStorage.getItem('projectId')
      me.$http.post('/doproject/materialsInfoByProject', { 'sourceType': 0, 'projectId': me.projectId }, res => {
        if (res.success) {
          for (var i = 0; i < res.data.length; i++) {
            if (res.data[i].materialType === 1) {
              me.videoList.push(res.data[i])
            } else if (res.data[i].materialType === 0) {
              me.docList.push(res.data[i])
            }
          }
        }
      })
    },
    changeVideo (url) {
      let me = this
      me.showflg = false
      me.$nextTick(() => {
        me.vUrl = url
        me.showflg = true
        setTimeout(function () {
          let vid = $('#video1')
          vid.bind('contextmenu', function () { return false })
        })
      })
    }
  }
}
</script>

<style scoped lang='scss'>
  div::-webkit-scrollbar{
    width:10px;
    height:10px;
    /**/
  }
  div::-webkit-scrollbar-track{
    background: #292929;
    border-radius:2px;
  }
  div::-webkit-scrollbar-thumb{
    background: #ffffff;
    border-radius:10px;
    width: 5px;
  }
  div::-webkit-scrollbar-corner{
    background: #179a16;
  }
  .video-list-bg{
    background-color: #292929;
    color: #ddd;
    margin-top: 20px;
    max-height:440px;
    overflow: auto;
    .video-img{
      padding: 5px;
      width: 100%;
      border-radius: 25px;
    }
    .video-bg{
      background-color: #292929;
    }
    .video-bg:hover {
      background-color: rgba(255,255,255,0.7);
      color: white;
    }
    .video-selected{
      background-color: rgba(255,255,255,0.6);
      color: white;
    }
  }
  .comment-title{
    border-bottom: 2px solid wheat;
    height: 40px;
  }
  .comment{
    border-bottom: 1px solid #ddd;
    margin-top: 10px;
    p{
      padding:20px;
    }
    .reply{
      background: #ddd;
      padding-top: 10px;
      margin-left: 20px;
      b{
        padding: 20px;
      }
      p{
        padding:20px;
      }
    }
  }
</style>
